import React, { useState } from 'react';
import { ProgressBar, Card } from './components/ui';
import { WizardQuestion } from './components/wizard';

interface QuestionData {
  id: string;
  title: string;
  placeholder: string;
  type: 'short-text' | 'long-text' | 'single-choice' | 'multiple-choice';
  required: boolean;
  options?: string[];
}

// 153
const WIZARD_QUESTIONS: QuestionData[] = [
  // Layer 1:  (1-5)
  {
    id: 'purpose',
    title: ' 1: ',
    placeholder: '',
    type: 'long-text',
    required: true,
  },
  {
    id: 'goals',
    title: ' 2: ',
    placeholder: '50%10%',
    type: 'long-text',
    required: true,
  },
  {
    id: 'timeline',
    title: ' 3: ',
    placeholder: '32024 ',
    type: 'short-text',
    required: true,
  },
  {
    id: 'budget',
    title: ' 4: ',
    placeholder: '',
    type: 'single-choice',
    required: true,
    options: ['10', '10-50', '50-100', '100', '']
  },
  {
    id: 'priority',
    title: ' 5: ',
    placeholder: '',
    type: 'multiple-choice',
    required: true,
    options: ['', '', '', '', '']
  },

  // Layer 2:  (6-10)
  {
    id: 'current_process',
    title: ' 6: ',
    placeholder: '',
    type: 'long-text',
    required: true,
  },
  {
    id: 'pain_points',
    title: ' 7: ',
    placeholder: '',
    type: 'long-text',
    required: true,
  },
  {
    id: 'stakeholders',
    title: ' 8: ',
    placeholder: '',
    type: 'single-choice',
    required: true,
    options: ['1-5', '6-20', '21-50', '51-100', '100']
  },
  {
    id: 'departments',
    title: ' 9: ',
    placeholder: '',
    type: 'multiple-choice',
    required: true,
    options: ['', '', '', '', 'IT', '']
  },
  {
    id: 'frequency',
    title: ' 10: ',
    placeholder: '',
    type: 'single-choice',
    required: true,
    options: ['', '', '1', '1', '']
  },

  // Layer 3:  (11-15)
  {
    id: 'current_tools',
    title: ' 11: ',
    placeholder: 'ExcelSlackSalesforce',
    type: 'long-text',
    required: true,
  },
  {
    id: 'data_format',
    title: ' 12: ',
    placeholder: '',
    type: 'multiple-choice',
    required: true,
    options: ['Excel/CSV', 'PDF', '', 'API', '']
  },
  {
    id: 'technical_skill',
    title: ' 13: ',
    placeholder: '',
    type: 'single-choice',
    required: true,
    options: ['PC', 'Excel', '', '', '']
  },
  {
    id: 'integration_needs',
    title: ' 14: ',
    placeholder: 'CRM',
    type: 'long-text',
    required: false,
  },
  {
    id: 'security_requirements',
    title: ' 15: ',
    placeholder: '',
    type: 'long-text',
    required: false,
  },
];

const App: React.FC = () => {
  const [currentStep, setCurrentStep] = useState(0);
  const [answers, setAnswers] = useState<Record<string, string | string[]>>({});
  const [showResult, setShowResult] = useState(false);
  const [isGenerating, setIsGenerating] = useState(false);
  const [isDraftSaved, setIsDraftSaved] = useState(false);

  // 24
  const DRAFT_KEY = 'business-improvement-draft';
  const DRAFT_TIMESTAMP_KEY = 'business-improvement-draft-timestamp';

  // LocalStorage
  const saveDraft = () => {
    const draftData = {
      currentStep,
      answers,
      timestamp: new Date().toISOString(),
    };
    localStorage.setItem(DRAFT_KEY, JSON.stringify(draftData));
    localStorage.setItem(DRAFT_TIMESTAMP_KEY, new Date().toISOString());
    setIsDraftSaved(true);
    
    // 3
    setTimeout(() => setIsDraftSaved(false), 3000);
  };

  // LocalStorage
  const loadDraft = () => {
    try {
      const draftData = localStorage.getItem(DRAFT_KEY);
      const timestamp = localStorage.getItem(DRAFT_TIMESTAMP_KEY);
      
      if (draftData && timestamp) {
        const saveTime = new Date(timestamp);
        const now = new Date();
        const hoursDiff = (now.getTime() - saveTime.getTime()) / (1000 * 60 * 60);
        
        // 24
        if (hoursDiff < 24) {
          const parsed = JSON.parse(draftData);
          setCurrentStep(parsed.currentStep || 0);
          setAnswers(parsed.answers || {});
          return true;
        } else {
          // 24
          localStorage.removeItem(DRAFT_KEY);
          localStorage.removeItem(DRAFT_TIMESTAMP_KEY);
        }
      }
    } catch (error) {
      console.error(':', error);
      localStorage.removeItem(DRAFT_KEY);
      localStorage.removeItem(DRAFT_TIMESTAMP_KEY);
    }
    return false;
  };

  // 
  React.useEffect(() => {
    loadDraft();
  }, []);

  // 
  React.useEffect(() => {
    if (Object.keys(answers).length > 0) {
      const timer = setTimeout(saveDraft, 2000); // 2
      return () => clearTimeout(timer);
    }
    return undefined; // useEffectundefinedcleanup
  }, [answers, currentStep]);

  const handleAnswer = (questionId: string, answer: string | string[]) => {
    setAnswers(prev => ({ ...prev, [questionId]: answer }));
  };

  const handleNext = () => {
    if (currentStep < WIZARD_QUESTIONS.length - 1) {
      setCurrentStep(prev => prev + 1);
    } else {
      handleFinish();
    }
  };

  const handlePrev = () => {
    if (currentStep > 0) {
      setCurrentStep(prev => prev - 1);
    }
  };

  const handleFinish = async () => {
    setIsGenerating(true);
    
    // 3
    setTimeout(() => {
      setIsGenerating(false);
      setShowResult(true);
    }, 3000);
  };

  // /specify
  const generateSpecifyCommand = () => {
    const purpose = answers.purpose || '';
    const goals = answers.goals || '';
    const currentProcess = answers.current_process || '';
    const painPoints = answers.pain_points || '';
    const stakeholders = answers.stakeholders || '';
    const departments = Array.isArray(answers.departments) ? answers.departments.join('') : (answers.departments || '');
    const frequency = answers.frequency || '';
    const currentTools = answers.current_tools || '';
    const integration = answers.integration_needs || '';
    
    return `/specify "${purpose}${goals}${currentProcess}${painPoints}${stakeholders}${departments}${frequency}${currentTools}${integration}"`;
  };

  // JSON
  // 
  /* const generateStructuredJSON = () => {
    return {
      "project": {
        "name": "",
        "purpose": answers.purpose || "",
        "goals": answers.goals || "",
        "timeline": answers.timeline || "",
        "budget": answers.budget || ""
      },
      "stakeholders": {
        "count": answers.stakeholders || "",
        "departments": Array.isArray(answers.departments) ? answers.departments : (answers.departments ? [answers.departments] : []),
        "frequency": answers.frequency || ""
      },
      "current_state": {
        "process": answers.current_process || "",
        "pain_points": answers.pain_points || "",
        "tools": answers.current_tools || "",
        "data_format": answers.data_format || ""
      },
      "technical_requirements": {
        "tech_level": answers.technical_skill || "",
        "integration": answers.integration_needs || "",
        "security": answers.security_requirements || ""
      },
      "priorities": Array.isArray(answers.priority) ? answers.priority : (answers.priority ? [answers.priority] : [])
    };
  }; */

  // Markdown
  // 
  /* const generateMarkdownSpec = () => {
    const json = generateStructuredJSON();
    return `# ${json.project.name}

## 
${json.project.purpose}

## KPI
${json.project.goals}

## 
${json.current_state.process}

### 
${json.current_state.pain_points}

## UI
- 
- 
- 
- 

## 
${json.technical_requirements.integration}

### Google Workspace
- Google Sheets: 
- Google Calendar: 
- Gmail: 
- Google Drive: 

## 
${json.technical_requirements.security}

### 
- 
- API
- 

## 
- 
- 
- 

## 
- 
- 
- 

---
*: ${new Date().toLocaleString('ja-JP')}*
*24*`;
  }; */

  const currentQuestion = WIZARD_QUESTIONS[currentStep];
  
  // : currentQuestion
  if (!currentQuestion) {
    console.error('Current question not found, resetting to step 0');
    setCurrentStep(0);
    return null; // 
  }
  const progress = ((currentStep + 1) / WIZARD_QUESTIONS.length) * 100;

  if (isGenerating) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <Card className="p-8 text-center max-w-md">
          <div className="mb-4">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto"></div>
          </div>
          <h2 className="text-xl font-bold mb-2">...</h2>
          <p className="text-gray-600"></p>
        </Card>
      </div>
    );
  }

  if (showResult) {
    const specifyCommand = generateSpecifyCommand();
    // /specify
    // const structuredJSON = generateStructuredJSON();
    // const markdownSpec = generateMarkdownSpec();
    
    const copyToClipboard = async (text: string, type: string) => {
      try {
        await navigator.clipboard.writeText(text);
        alert(`${type}`);
      } catch (err) {
        console.error('Failed to copy text: ', err);
      }
    };
    
    // 
    /*
    const downloadFile = (content: string, filename: string, contentType: string) => {
      const blob = new Blob([content], { type: contentType });
      const url = window.URL.createObjectURL(blob);
      const link = document.createElement('a');
      link.href = url;
      link.download = filename;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      window.URL.revokeObjectURL(url);
    };
    */
    
    return (
      <div style={{backgroundColor: 'var(--color-gray-light)'}} className="min-h-screen py-8">
        <div className="max-w-4xl mx-auto px-4">
          <div style={{backgroundColor: 'var(--color-white)'}} className="p-8 border-0">
            <h1 className="text-2xl font-bold mb-6" style={{color: 'var(--color-primary)'}}>
              
            </h1>
            
            {/* /specify  */}
            <div className="mb-8">
              <h2 className="text-lg font-semibold mb-3" style={{color: 'var(--color-black)'}}>/specify </h2>
              <p className="text-sm mb-2" style={{color: 'var(--color-black)', opacity: '0.8'}}>GitHub spec-kit</p>
              <div style={{backgroundColor: 'var(--color-gray-light)'}} className="p-4 mb-4 font-mono text-sm">
                {specifyCommand}
              </div>
              <div className="btn-group">
                <button 
                  onClick={() => copyToClipboard(specifyCommand, '/specify')}
                  className="btn btn-primary btn-full-width"
                >
                  /specify
                </button>
              </div>
            </div>
            
            <div className="btn-group">
              <button 
                onClick={() => {
                  setCurrentStep(0);
                  setAnswers({});
                  setShowResult(false);
                }}
                className="btn btn-full-width"
              >
                
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div style={{backgroundColor: 'var(--color-gray-light)'}} className="min-h-screen">
      <div style={{backgroundColor: 'var(--color-primary)', color: 'var(--color-white)'}} className="py-6">
        <div className="max-w-4xl mx-auto px-4">
          <h1 className="text-2xl font-bold"></h1>
          <p style={{color: 'var(--color-white)', opacity: '0.9'}} className="mt-2">15AI</p>
        </div>
      </div>

      <div className="max-w-4xl mx-auto px-4 py-8">
        <div className="mb-8">
          <ProgressBar 
            value={progress}
            showPercentage={true}
            label={` ${currentStep + 1} / ${WIZARD_QUESTIONS.length}`}
            className="mb-4"
          />
          <div className="flex justify-between items-center">
            <p className="text-gray-600">
              {currentStep < 5 && "Layer 1: "}
              {currentStep >= 5 && currentStep < 10 && "Layer 2: "}  
              {currentStep >= 10 && "Layer 3: "}
            </p>
            {/*  */}
            {isDraftSaved && (
              <div className="flex items-center text-green-600 text-sm">
                <svg className="w-4 h-4 mr-1" fill="currentColor" viewBox="0 0 20 20">
                  <path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd" />
                </svg>
                
              </div>
            )}
          </div>
          <p className="text-xs text-gray-500 text-center mt-2">
             24
          </p>
        </div>

        <Card className="p-8 mb-8">
          <WizardQuestion
            question={currentQuestion}
            value={answers[currentQuestion.id] || ''}
            onChange={(value) => handleAnswer(currentQuestion.id, value)}
          />
        </Card>

        <div className="flex gap-4">
          <button
            onClick={handlePrev}
            disabled={currentStep === 0}
            className="btn flex-1"
          >
            
          </button>
          
          <button
            onClick={handleNext}
            disabled={currentQuestion.required && !answers[currentQuestion.id]}
            className="btn btn-primary flex-1"
          >
            {currentStep === WIZARD_QUESTIONS.length - 1 ? '' : ''}
          </button>
        </div>
      </div>
    </div>
  );
};

export default App;